// Brazilian date mask utilities

/**
 * Applies Brazilian date mask (DD/MM/YYYY) to a string
 * @param value - The input value to mask
 * @returns The masked date string
 */
export const applyDateMask = (value: string): string => {
  // Remove all non-numeric characters
  const numbers = value.replace(/\D/g, '');
  
  // Apply mask DD/MM/YYYY
  if (numbers.length <= 2) {
    return numbers;
  } else if (numbers.length <= 4) {
    return `${numbers.slice(0, 2)}/${numbers.slice(2)}`;
  } else if (numbers.length <= 8) {
    return `${numbers.slice(0, 2)}/${numbers.slice(2, 4)}/${numbers.slice(4)}`;
  } else {
    return `${numbers.slice(0, 2)}/${numbers.slice(2, 4)}/${numbers.slice(4, 8)}`;
  }
};

/**
 * Removes the date mask and returns only numbers
 * @param value - The masked date string
 * @returns The unmasked numbers
 */
export const removeDateMask = (value: string): string => {
  return value.replace(/\D/g, '');
};

/**
 * Validates if a date string is in valid Brazilian format (DD/MM/YYYY)
 * @param value - The date string to validate
 * @returns True if valid, false otherwise
 */
export const isValidBrazilianDate = (value: string): boolean => {
  const regex = /^(0[1-9]|[12][0-9]|3[01])\/(0[1-9]|1[0-2])\/\d{4}$/;
  if (!regex.test(value)) return false;
  
  const parts = value.split('/');
  const day = parseInt(parts[0], 10);
  const month = parseInt(parts[1], 10);
  const year = parseInt(parts[2], 10);
  
  const date = new Date(year, month - 1, day);
  return date.getDate() === day && 
         date.getMonth() === month - 1 && 
         date.getFullYear() === year;
};

/**
 * Converts Brazilian date format (DD/MM/YYYY) to ISO date format (YYYY-MM-DD)
 * @param brazilianDate - Date in DD/MM/YYYY format
 * @returns Date in YYYY-MM-DD format
 */
export const brazilianToISODate = (brazilianDate: string): string => {
  if (!isValidBrazilianDate(brazilianDate)) return '';
  
  const parts = brazilianDate.split('/');
  const day = parts[0];
  const month = parts[1];
  const year = parts[2];
  
  return `${year}-${month}-${day}`;
};

/**
 * Converts ISO date format (YYYY-MM-DD) to Brazilian date format (DD/MM/YYYY)
 * @param isoDate - Date in YYYY-MM-DD format
 * @returns Date in DD/MM/YYYY format
 */
export const isoToBrazilianDate = (isoDate: string): string => {
  if (!isoDate) return '';
  
  const date = new Date(isoDate);
  if (isNaN(date.getTime())) return '';
  
  const day = date.getDate().toString().padStart(2, '0');
  const month = (date.getMonth() + 1).toString().padStart(2, '0');
  const year = date.getFullYear();
  
  return `${day}/${month}/${year}`;
};

/**
 * Formats a date input event to apply Brazilian date mask
 * @param e - The input event
 * @param setValue - Function to set the input value
 */
export const handleDateInputChange = (
  e: React.ChangeEvent<HTMLInputElement>,
  setValue: (value: string) => void
) => {
  const input = e.target;
  const cursorPosition = input.selectionStart || 0;
  const oldValue = input.value;
  const newValue = applyDateMask(input.value);
  
  setValue(newValue);
  
  // Restore cursor position after mask application
  setTimeout(() => {
    const newCursorPosition = calculateNewCursorPosition(oldValue, newValue, cursorPosition);
    input.setSelectionRange(newCursorPosition, newCursorPosition);
  }, 0);
};

/**
 * Calculates the new cursor position after applying the mask
 */
const calculateNewCursorPosition = (
  oldValue: string,
  newValue: string,
  oldCursorPosition: number
): number => {
  const oldNumbers = removeDateMask(oldValue);
  const newNumbers = removeDateMask(newValue);
  
  // If we added a separator, move cursor forward
  if (newValue.length > oldValue.length) {
    return oldCursorPosition + 1;
  }
  
  // If we removed a separator, move cursor backward
  if (newValue.length < oldValue.length) {
    return Math.max(0, oldCursorPosition - 1);
  }
  
  return oldCursorPosition;
};
